<template>
  <div class="currencyWrapper">
    <div class="greenBlock">
      <p>Курси валют</p>
    </div>

    <div class="tableWrapper">
      <ul class="currencyTable">
        <li class="currencyList listHeader">
          <div class="itm"></div>
          <div class="itm">Купівля</div>
          <div class="itm">Продаж</div>
        </li>
        <li v-for="item in rate" :key="item.ccy" class="currencyList">
          <div class="itm">{{currencyCode(item.currencyCodeA)}}</div>
          <div class="itm">{{item.rateBuy}}</div>
          <div class="itm">{{item.rateSell}}</div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'Currency',
  data () {
    return {
      timeout: 0,
      rate: [],
      curencyCodeList: {
        840: 'USD',
        980: 'UAH',
        978: 'EUR'
      }
    }
  },
  methods: {
    test () {
      return new Promise((resolve) => {
        this.timeout = 2500
        setTimeout(() => {
          resolve(this.timeout)
        }, this.timeout)
      })
    },
    currencyCode (code) {
      return this.curencyCodeList[code]
    },
    loadCurencyRate () {
      axios.get('https://api.monobank.ua/bank/currency',
        {
          headers: {
            'Access-Control-Allow-Origin': '*',
            'Access-Control-Allow-Methods': 'GET,PUT,POST,DELETE,PATCH,OPTIONS'
          }
        }).then((res) => {
        this.rate = res.data.filter((el) => [840, 978].includes(el.currencyCodeA) && el.currencyCodeB === 980)
      })
    }
  },
  mounted () {
    this.loadCurencyRate()
  }
}
</script>

